import { ReactNode } from "react";
import cl from "./Button.module.css";




export const Button = ({
  children = "Click me",
  type,
  size = "medium",
  variant = "square",
}) => {
  return (
    <button
      className={[cl.button, cl[size], cl[variant]].join(" ")}
      type={type}>
      {children}
    </button>
  );
};
